import React from 'react'
import StarRating from './utils/StarRating'
import { Link } from 'react-router-dom'

const LotPreview = () => {
    return (
        <tr className='lot-preview'>
            <td>Android</td>
            <td><Link to='lot'>ProjectSuperEssence.net Top Rang Step - Season 3, l8k-2568, ,  , 150., Avatar</Link></td>
            <td>
                <Link to='/user' className='lot-preview-user'>
                    <div className='img'>
                        <img src='imgs/user2.png' alt=' '/>
                        <div className='indicator online'></div>
                    </div>
                    <div>
                        <h5 className='achromat-2 mb-1'> </h5>
                        <div className='achromat-3 mb-1'>@Irishka1911</div>
                        <StarRating rate={5} className='justify-content-start fs-08'/>
                        <div>&nbsp; &nbsp; 2019&nbsp;.</div>
                    </div>
                </Link>
            </td>
            <td><div className='color-1 fw-7'>3000&nbsp;.</div></td>
        </tr>
    );
};

export default LotPreview;