import React from 'react'
import { NavLink } from 'react-router-dom'
import { BiUserCircle, BiNews, BiHistory, BiBookOpen, BiEnvelope, BiWallet, BiConversation, BiLogOut, BiLike } from "react-icons/bi"

export default function AccountMenu() {
    return (
        <nav className='menu'>
            <ul>
                <li>
                    <NavLink to='profile'>
                        <BiUserCircle/>
                        <span></span>
                    </NavLink>
                </li>
                <li>
                    <NavLink to='ads'>
                        <BiNews/>
                        <span> </span>
                    </NavLink>
                </li>
                <li>
                    <NavLink to='purchase-history'>
                        <BiHistory/>
                        <span> </span>
                    </NavLink>
                </li>
                <li>
                    <NavLink to='sales'>
                        <BiBookOpen/>
                        <span> </span>
                    </NavLink>
                </li>
                <li>
                    <NavLink to='finance'>
                        <BiWallet/>
                        <span></span>
                    </NavLink>
                </li>
                <li>
                    <NavLink to='messages'>
                        <BiEnvelope/>
                        <span></span>
                    </NavLink>
                </li>
                <li>
                    <NavLink to='help'>
                        <BiConversation/>
                        <span></span>
                    </NavLink>
                </li>
                <li>
                    <NavLink to='reviews'>
                        <BiLike/>
                        <span></span>
                    </NavLink>
                </li>
                <li>
                    <NavLink to='exit'>
                        <BiLogOut/>
                        <span></span>
                    </NavLink>
                </li>
            </ul>
        </nav>
    )
}