import React from 'react'
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import InputPassword from '../components/utils/InputPassword'
import { Link } from 'react-router-dom'

const Registration = () => {
    return (
        <main>
            <Container>
                <section className='pt-4 pt-sm-5 mb-6'>
                    <Row className='justify-content-center'>
                        <Col xs={12} lg={10} xl={8} xxl={7}>
                            <h1 className='text-center'></h1>
                            <form>
                                <Row className='g-3 g-sm-4 align-items-center'>
                                    <Col md={4}>
                                        <h6 className='mb-0'>:</h6>
                                    </Col>
                                    <Col md={8}>
                                        <input type='text' placeholder='' />
                                    </Col>
                                    <Col md={4}>
                                        <h6 className='mb-0'>:</h6>
                                    </Col>
                                    <Col md={8}>
                                        <input type='text' placeholder='' />
                                    </Col>
                                    <Col md={4}>
                                        <h6 className='mb-0'>:</h6>
                                    </Col>
                                    <Col md={8}>
                                        <input type='text' placeholder='' />
                                    </Col>
                                    <Col md={4}>
                                        <h6 className='mb-0'>Email:</h6>
                                    </Col>
                                    <Col md={8}>
                                        <input type='email' placeholder='Email' />
                                    </Col>
                                    <Col md={4}>
                                        <div>  :</div>
                                    </Col>
                                    <Col md={8}>
                                        <input type='text' placeholder='' />
                                    </Col>
                                    <Col md={4}>
                                        <h6 className='mb-0'>:</h6>
                                    </Col>
                                    <Col md={8}>
                                        <InputPassword />
                                    </Col>
                                    <Col md={4}>
                                        <h6 className='mb-0'> :</h6>
                                    </Col>
                                    <Col md={8}>
                                        <InputPassword />
                                    </Col>
                                </Row>
                                <label className='mt-4'>
                                    <input type='checkbox' />
                                    <span> </span>
                                </label>
                                <button type='submit' className='btn-5 fs-13 mt-4 mx-auto'></button>
                                <p className='text-center achromat-3 fs-08 mt-3'>   ,   <br/> <Link to='/'> </Link></p>
                                <h6 className='mt-4 mt-sm-5 text-center'>   ? <Link to='/login' className='color-1'></Link></h6>
                            </form>
                        </Col>
                    </Row>
                </section>
            </Container>
        </main>
    );
};

export default Registration;