import React, {useState} from 'react'
import StarRating from '../../components/utils/StarRating'
import Review from '../../components/Review'
import { Link } from 'react-router-dom'
import { FiArrowLeft } from "react-icons/fi"

const Reviews = () => {
    const [tab, setTab] = useState(0)

    return (
        <div className='main'>
            <div className='d-sm-flex align-items-center justify-content-between mb-4'>
                <div className='d-flex align-items-center'>
                    <Link to='/account' className='btn-1 p-2 me-4 d-lg-none'><FiArrowLeft className='fs-15'/></Link>
                    <h4 className='color-1 mb-0'></h4>
                </div>
                <div className='d-flex align-items-center mt-3 mt-sm-0'>
                    <span className='me-4'> :</span>
                    <StarRating rate={3.7} />
                </div>
            </div>

            <div className='tabs-group'>
                <button type="button" className={(tab===0)?'active':''} onClick={()=>setTab(0)}>  </button>
                <button type="button" className={(tab===1)?'active':''} onClick={()=>setTab(1)}> </button>
            </div>

            {
                (tab === 0)
                ? <div>
                    <Review myReview={false} text={',    ,   ,       .      ,            .  ,           ,   , ,         , -     .'}/>
                    <Review myReview={false} text={'                    . '}/>
                    <Review myReview={false} text={'        -          .     ,   :    ,        .   ,   ,    ,       .    ,                    .'}/>
                    <Review myReview={false} text={'                    . '}/>
                </div>
                : <div>
                    <Review myReview={true} text={',    ,   ,       .      ,            .  ,           ,   , ,         , -     .'}/>
                    <Review myReview={true} text={'                    . '}/>
                    <Review myReview={true} text={'        -          .     ,   :    ,        .   ,   ,    ,       .    ,                    .'}/>
                    <Review myReview={true} text={'                    . '}/>
                </div>
            }
        </div>
    );
};

export default Reviews;