import React, { useState } from 'react'
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Modal from 'react-bootstrap/Modal'
import StarRating from '../components/utils/StarRating'
import InputRating from '../components/utils/InputRating'
import ReviewBlock from '../components/ReviewBlock'
import { VscChromeClose } from "react-icons/vsc"
import Select from 'react-select'

const optionsLots = [
    { value: '1', label: ' 1' },
    { value: '2', label: ' 2' },
    { value: '3', label: ' 3' },
    { value: '4', label: ' 4' },
    { value: '5', label: ' 5' },
    { value: '6', label: ' 6' },
]

const UserPage = () => {
    const [showReview, setShowReview] = useState(false)

    const handleCloseReview = () => setShowReview(false)
    const handleShowReview = () => setShowReview(true)
    return (
        <>
        <main>
            <Container>
                <section className='user-page pt-5 mb-6'>
                    <Row className='gy-4 gy-sm-5 gx-4 gx-xxl-5'>
                        <Col sm={5} md={4} lg={3}>
                            <img src='imgs/user.png' alt='' className='img'/>
                        </Col>
                        <Col sm={7} md={8} lg={4} xl={3}>
                            <h4> </h4>
                            <StarRating rate={4.1} className='justify-content-start'/>
                            <p className='mt-4'>    2019 .</p>
                            <p className='mt-2'> : <strong>100</strong></p>
                            <button type='button' onClick={handleShowReview} className='btn-6 mt-4'> </button>
                        </Col>
                        <Col xs={12} lg={5} xl={6}>
                            <Row className='info g-2 g-sm-4'>
                                <Col xs={5}>:</Col>
                                <Col xs={7}></Col>
                                <Col xs={5}>:</Col>
                                <Col xs={7}></Col>
                                <Col xs={5}>:</Col>
                                <Col xs={7}>Irishka1911</Col>
                                <Col xs={5}>:</Col>
                                <Col xs={7}></Col>
                                <Col xs={5}> ::</Col>
                                <Col xs={7}>11.11.2001</Col>
                            </Row>
                        </Col>
                        <Col xs={12} lg={7}>
                            <div className='d-flex align-items-center mb-4'>
                                <span>:</span>
                                <select className='w-50 ms-4'>
                                    <option> </option>
                                    <option>5 </option>
                                    <option>4 </option>
                                    <option>3 </option>
                                    <option>2 </option>
                                    <option>1 </option>
                                </select>
                            </div>
                            <ReviewBlock />
                            <ReviewBlock />
                            <ReviewBlock />
                        </Col>
                    </Row>
                </section>
            </Container>
        </main>

        <Modal show={showReview} onHide={handleCloseReview}>
            <Modal.Body>
                <div className='d-flex align-items-center justify-content-between mb-4'>
                    <h3 className='color-1'> </h3>
                    <button type='button' onClick={handleCloseReview} className='btn-4 px-3 py-2'>
                        <VscChromeClose/>
                    </button>
                </div>
                
                <form>
                    <div className='mb-2'> :</div>
                    <Select name="lot" placeholder='' classNamePrefix="react-select" options={optionsLots} isClearable={true} isSearchable={true} />
                    <div className='mt-4 mb-2'> :</div>
                    <InputRating className='fs-15' />
                    <div className='mt-4 mb-2'> :</div>
                    <textarea rows={5} placeholder=''></textarea>
                    <button type='submit' className='btn-5 w-100 mt-4'></button>
                </form>
            </Modal.Body>
        </Modal>
        </>
    );
};

export default UserPage;